import T from 'template-binding';

class NotifyAnnotation {
  constructor (model, name) {
    let expr = T.Expr.get(model.getAttribute(name));
    this.model = model;
    this.name = expr.name;
  }

  effect (value) {
    this.model.fire('-notify', {
      name: this.name,
      value: value,
    });
  }
}

export default NotifyAnnotation;



